import { useCallback, useState } from "react";

import {
  createTheme,
  ThemeProvider,
  CssBaseline,
  darkScrollbar,
  Tabs,
  Tab,
} from "@mui/material";
import { jaJP } from "@mui/material/locale";

import TAB_VALUES from "./constants/DEFAULT_VALUES";
import ItemList from "./ItemList";
import TaskList from "./TaskList";
import Tracker from "./Tracker";
import TopBar from "./TopBar";

const darkTheme = createTheme(
  {
    palette: {
      mode: "dark",
    },
    components: {
      MuiCssBaseline: {
        styleOverrides: {
          body: {
            ...darkScrollbar(),
            height: "100%",
          },
        },
      },
    },
  },
  jaJP
);
function App() {
  const [tabValue, setTabValue] = useState(TAB_VALUES.TAB_ITEMS);

  const switchTabs = () => {
    switch (tabValue) {
      case TAB_VALUES.TAB_TASKS:
        return <TaskList />;
      case TAB_VALUES.TAB_ITEMS:
        return <ItemList />;
      default:
        return <Tracker />;
    }
  };
  const handleChange = useCallback((_event: any, newValue: number) => {
    setTabValue(newValue);
  }, []);
  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <TopBar />
      <Tabs value={tabValue} onChange={handleChange} centered>
        <Tab label="Tasks" />
        <Tab label="Items" />
      </Tabs>
      {switchTabs()}
    </ThemeProvider>
  );
}

export default App;
